async function getBackendMessage() {
    try {
        // 把 localhost 改成 127.0.0.1
        const res = await fetch('http://127.0.0.1:3001');

        if (!res.ok) {
            throw new Error('Failed to fetch data from backend');
        }
        const data = await res.json();
        return data.message;
    } catch (error) {
        console.error(error); // 這行會把詳細錯誤印在你的 VSCode/WebStorm 終端機
        return '連線到後端失敗了... 檢查一下 api 是不是有跑起來？';
    }
}

export default async function HomePage() {
  const message = await getBackendMessage();

  return (
      <main style={{ fontFamily: 'sans-serif', textAlign: 'center', marginTop: '50px' }}>
        <h1 style={{ fontSize: '2rem' }}>雪拼 Snow Shopping 前端</h1>
        <p style={{ marginTop: '20px', fontSize: '1.2rem', color: '#4A5568' }}>
          後端說： <span style={{ fontWeight: 'bold', color: '#2C5282' }}>{message}</span>
        </p>
      </main>
  );
}